.app {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: bisque;
}

body {
    padding: 0;
    margin: 0;
}

.header {
    color:black;
}

.board {
    width: 500px;
    height: 500px;
    background-color: aquamarine;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    align-items:stretch;
}

.row {
    flex: 33%;
    display: flex;
    flex-direction: row;
    border: 1px solid black;
}

.square{
    flex: 33%;
    font-size: 30px;
    cursor: pointer;
    display: grid;
    place-items: center;
    border: 1px solid black;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
}

.square:active{
    background-color: #64f4c4;
}

.footer {
    padding-top: 1.5%;
    color:black;
}